.knowledges {
  .knowledgesContent {
    font-family: $font-3;
    min-height: $minHeight;
    width: 100%;
    position: relative;
    overflow: hidden;
    display: grid;
    grid-template-columns: 28% 1fr 1fr;
    grid-template-rows: 75% 25%;
    grid-template-areas: 
      "L E E"
      "O O H";

    .languagesFrameworks, .experience, .otherSkills, .hobbies {
      background: $color-1;
      border-radius: $borderRadius;
      padding: .8rem .8rem 1rem .8rem;
    }
    h3 {
      font-size: 1.2rem;
      margin-bottom: 1rem;
      font-family: $font-3;
      color: $ocre;
      text-transform: uppercase;
    }

    .languagesFrameworks {
      grid-area: L;
      margin-bottom: .5rem;
      display: grid;
      grid-template-columns: 1fr;
      grid-template-rows: 1fr 1fr;
      grid-template-areas: 
        "L"
        "F";

      .languageDisplay {
        grid-area: L;
      }
      .frameworkDisplay {
        grid-area: F;
      }
      h3 {
        margin-bottom: 0.3rem;
      }

      .years {
        font-size: .65rem;
        display: flex;
        justify-content: space-between;
        color: $white;

        span {
          width: 36%;

          &:nth-child(2){
            text-align: center; 
            width: 20%;
          }
          &:nth-child(3){
            text-align: right;
            width: 44%;
          }
        
          &:nth-child(2):before,
          &:nth-child(3):before {
            content:'';
            background: rgba(180, 179, 179, 0.507);
            position: absolute;
            height: 175px;
            width: 1px;
            transform: translate(9px, 19px);
          }
        }
      }
      .languagesList {
        padding-top: .5rem;

        li {
          color: $grisClair;
        }
        .progressBar {
          height: .4rem;
          background: transparent;
          border-radius: 50px;
          margin-top: 0.2rem;
          position: relative;

          &:after {
            content: '';
            position: absolute;
            background: $bleuMarine;
            border-radius: 50px;
            box-shadow: 0 0 0 .2px $grisClair;
            height: .4rem;
            width: 0;
            animation: line 2s ease-out forwards;

            @keyframes line {
              to {
                width: 90%;
              }
            }
          }
        }
      }
    }
    .experience {
      grid-area: E;
      font-family: $font-3;
      margin: 0 0 .5rem .5rem;
      display: grid;
      grid-template-columns: 1fr;
      grid-template-rows: 10% 1fr 1fr 1fr;
      grid-template-areas: 
        "1"
        "2"
        "3"
        "4";
      
      h3 {
        grid-area: 1;
      }
      h4 {
        font-size: 1.1rem;
        color: $terre;
      }
      h5 {
        color: $ocre;
        padding: .5rem 0 .5rem 1rem;
      }
      p {
        color: $white;
        text-align: justify;
        padding: 0 1rem;
      }
      .exp-1 {
        grid-area: 2;
        height: 90%;
      }
      .exp-2 {
        grid-area: 3;
      }
      .exp-3 {
        grid-area: 4;
        padding-top: 1rem;
      }
    }
    .otherSkills {
      grid-area: O;
      margin-right: .5rem;

      .list {
        display: flex;
        width: 80%;
        height: 100%;
        margin: 0 auto;
        justify-content: space-between;

        ul {
          height: 80%;
          display: grid;
          i {
            color: $terre;
            margin-right: .5rem;
          }
          span {
            color: $white;
          }
        }
      }
    }
    .hobbies {
      grid-area: H;
      padding-left: 1.5rem;

      ul {
        height: 80%;
        display: grid;

        li {
          i {
            position: absolute;
            color: $terre;
          }
          span {
            color: $white;
            position: absolute;
            transform: translateX(2.2rem);
          }
        }
      }
    }
  }
}
@media screen and (max-width: 950px){
  .knowledgesContent {
    h3 {
      font-size: 1.2rem !important;
    }

    .languagesList {
      padding-top: .2rem !important;
    }
    p {
      font-size: .8rem;
    }
    .otherSkills, .hobbies {
      font-size: .8rem;
      padding-bottom: 2rem !important;
    }
  }
}
@media screen and (max-width: 850px){

  .knowledgesContent {
    h4 {
      font-size: .9rem !important;
    }
    h5 {
      font-size: .6rem !important;
      padding: .2rem 1rem !important;
    }


      .languageDisplay{
        width: 75% !important;
      }
    

  }
}
@media screen and (max-width: 500px){
  .knowledgesContent {
    display: grid !important;
    grid-template-columns: 1fr !important;
    grid-template-rows: 1fr 1fr 180px 180px !important;
    
    .languagesFrameworks {
      grid-area: 1 !important;
    }
    .experience {
      grid-area: 2 !important;
      margin-left: 0 !important;
    }
    .otherSkills {
      grid-area: 3 !important;
      margin-right: 0 !important;
    }
    .hobbies {
      grid-area: 4 !important;
      margin-top: .5rem !important;
      padding-bottom: 2rem !important;
    }
  }
}