.home {

  .homeContent {
    background: linear-gradient(145deg, $color-1 30%, transparent 100%);
    min-height: $minHeight;
    width: 100%;
    min-height: 500px;
    border-radius: $borderRadius;
    position: relative;
    overflow: hidden;

    &:after {
      content: '';
      position: absolute;
      height: 100%;
      width: inherit;
      background: url('../../media/photo-profil-home.jpg') no-repeat center/cover;
      z-index: -1;
      filter: brightness(30%);
    }

    .content {
      position: absolute;
      font-family: $font-3;
      height: 100%;
      width: 100%;
      padding: 1rem;
      display: grid;
      grid-template-columns: 1fr;
      grid-template-rows: 1fr 1fr 1fr;
      grid-template-areas: 
        "1"
        "2"
        "3";

      h1 {
        grid-area: 1;
        font-size: 3rem;
        color: $ocre;
        text-transform: uppercase;
        display: flex;
        align-items: center;
      }

      .quoteArea {
        grid-area: 2;
        background: $bleuMarine;
        font-size: 1.5rem;
        font-style: italic;
        padding: 1rem;
        border-radius: 10px;
        transition: .5s;
        color: $white;
        display: flex;
        align-items: center;
        display: grid;
        grid-template-columns: 80% 20%;
        grid-template-rows: 1fr;
        grid-template-areas: 
          "A B";
      }

      .quote{
        grid-area: A;
      }

      button{
        grid-area: B;
        right: 1rem;
        background: $ocre;
        font-size: .8rem;
        padding: .5rem;
        margin-left: 1rem;
        border-radius: 10px;
        transition: .5s;
        color: $white;
        cursor: pointer;
        display: flex;
        align-items: center;
        display: flex;
        justify-content: flex-end;
      }

      .pdf {
        grid-area: 3;
        display: flex;
        align-items: center;
        justify-content: flex-end;
        a {
          font-size: 1.5rem;
          background: $terre;
          padding: 1rem 2rem;
          border-radius: $borderRadius;
          transition: 0.2s ease;

          &:hover {
            background: $ocre;
          }
        }
      }
    }
  }
}
@media screen and (max-width: 850px){
  .homeContent {
    margin: 0 !important;
    min-height: 600px !important;
  }
}
@media screen and (max-width:580px){
  .homeContent {
    &:after {
    background: url('../../media/photo-profil-home.jpg') no-repeat 60% 50%/cover !important;
    }
    h1 {
      font-size: 2rem !important;
    }
    h2 {
      font-size: 1.5rem !important;
    }
    .content{
      //top: 19% !important;

      .quoteArea{
        display: none !important;
      }
    }
  }
}