.contactContent {
  background: $bleuMarine;
  border-radius: $borderRadius;
  font-family: $font-3;
  width: 100%;
  overflow: hidden;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 15% 70% 15%;
  grid-template-areas: 
    "1"
    "2"
    "3";

  .header {
    grid-area: 1;
    width: 100%;
    position: relative;
    background: $ocre;
    height: 340px;

    &:before {
      content: '';
      width: 100%;
      height: 100%;
      position: absolute;
      background: url(../../media/map.jpg) no-repeat center top/cover;
      filter: grayscale(100%);
    }
    &:after {
      content: '';
      width: 100%;
      height: 100%;
      position: absolute;
      top: 30px;
      background: url(../../media/shape.svg) no-repeat center /cover;

    }
  }
  .contactBox {
    grid-area: 2;
    background: $white;
    margin: 0 auto;
    padding: 1rem 2rem;
    z-index: 2;
    border-radius: $borderRadius;
    box-shadow: 0 3px 10px #333;
    width: 50%;
    min-width: 440px;
    min-height: 400px;

    h1 {
      text-align: center;
      margin-top: 0.3rem;
      position: relative;
      color: $color-1;

    /*  &:after {
        content: '';
        height: 1px;
        width: 50%;
        background: $color-1;
        position: absolute;
        bottom: -25px;
        left: 50%;
        transform: translateX(-50%);
      }*/
    }
    ul {
      height: 72%;
      width: inherit;
      display: grid;
      margin-top: 3.5rem;

      li {
        position: relative;
      }

      i {
        font-size: 1.4rem;
        position: absolute;
        transform: translateY(2px);
        color: $terre;
      }
      span {
        font-size: 1rem;
        position: absolute;
        transform: translateX(52px);
        background: transparent;
        border: none;
        outline: none;
        width: 350px;
        transition: .2s ease;
      }
      .clickInput:hover {
        transform: translateX(56px);
        cursor: pointer;
      }
    }
  }
  .socialNetwork {
    grid-area: 3;
    @include verticalCenter;

    ul {
      display: flex;
      text-align: center;
      width: 100%;
      justify-content: space-around;

      a {
        width: 20%;
        font-size: 1.2rem;
        background: $terre;
        border-radius: $borderRadius;
        display: inline-block;
        padding: 0.7rem 0;
        box-shadow: $shadow;
        transform: scaleY(0);
        transform-origin: center;
        transition: .2s ease;
        transition: scale .1s;

        &:hover {
          background: $ocre;
        }

        i {
          color: $color-1;
          margin-top: .3rem;
        }
      }
      //ANIMATION
      @mixin networkAnim($timeout) {
        animation: pop 1s forwards;
        animation-delay: $timeout;

      }
      @keyframes pop {
        to {
          transform: scaleY(1);
        }
      }
      a:nth-child(1) {
        @include networkAnim(.4s);
      }
      a:nth-child(2) {
        @include networkAnim(.8s);
      }
      a:nth-child(3) {
        @include networkAnim(1.2s);
      }
      a:nth-child(4) {
        @include networkAnim(1.6s);
      }
    }
  }
}
@media screen and (max-width: 500px){
  .contactContent {
    .contactBox {
      min-width: 100% !important;
    }
    span {
      font-size: .9rem !important;
    }
    h4 {
      font-size: .9rem;
    }
  }
}

.form-content {
  display: grid;
  grid-template-rows: repeat(4, 1fr) 105px;
  padding-top: 2rem;
}

input,
textarea {
  background: none;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  font-size: 1.1rem;
  border-bottom: 1px solid $black;
  border-top: none;
  border-left: none;
  border-right: none;
}

input::placeholder, 
textarea::placeholder {
 color: rgb(190, 188, 188);
}

/*
input,
.email-content,
textarea {
  width: 90%;
  margin: 0 auto;
}
*/

input {
  padding: 24px 5px 12px 5px;
}

.email-content {
  position: relative;
}

.email-content input {
  width: 100%;
}

#not-mail {
  display: none;
  position: absolute;
  top: 4px;
  color: #ff4d4d;
  right: 0;
  transform-origin: 50% 50%;
}

@keyframes dongle {
  0% {
    transform: translate(0px, 0px);
  }
  10% {
    transform: translate(-10px, 0px);
  }
  20% {
    transform: translate(10px, 0px);
  }
  30% {
    transform: translate(-10px, 0px);
  }
  40% {
    transform: translate(10px, 0px);
  }
  50% {
    transform: translate(-10px, 0px);
  }
  60% {
    transform: translate(10px, 0px);
  }
  70% {
    transform: translate(-10px, 0px);
  }
  80% {
    transform: translate(10px, 0px);
  }
  90% {
    transform: translate(-10px, 0px);
  }
  100% {
    transform: translate(0px, 0px);
  }
}

textarea {
  resize: none;
  padding: 24px 5px;
}

.button {
  background: $ocre;
  border: none;
  color: $color-1;
  font-size: 1.3rem;
  margin-top: 2rem;
  padding-bottom: 1.3rem;

  cursor: pointer;
  transition: .4s;
  width: 100%;
}

.button:hover {
  letter-spacing: 10px;
}

.error::placeholder {
  color: #ff4d4d;

}
.form-message {
  margin-top: 10px;
  padding: 12px;
  opacity: 0;
  transition: 0.2s ease;
  color: black;
  border-radius: 4px;
  box-shadow: 0 0 2px rgba(51, 51, 51, 0.3);
}